.projects-view-container{
  margin-bottom: 100px;

  .projects-line-one, .projects-line-two, .projects-line-three{
    white-space: nowrap;
    overflow: hidden;
    word-spacing: -10px;
    background-color:none;
    span{
      background-color: none;
      font-size: 0.6rem;
    }
  }
  .projects-line-one{
    height: 50px;
    span{
      line-height: 50px;
    }
    h1{
      // transform: translateX(-150%);
    }
  }
  .projects-line-two{
    line-height: 50px;
    span{
      height: 50px;
    }
    :not(span.outline){
      color: rgba(255,255,255,0.5)
    }
    .outline{
      -webkit-text-stroke: 2px rgba(255,255,255,0.5);
    }
    h1{
      transform: translateX(-170%);
    }
  }

  .project-box{
    margin-top: 50px;
  }
  .detail-line{
    display: flex;
    align-items: center;
    img{
      height: 40px;
    }
    .empty{
      width: 30px;
    }
  }

  .tech-line{
    display: flex;
    align-items: center;
    img{
      height: 40px;
    }
    .icon-wrapper{
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 65px;
      .tooltipText{
        visibility: hidden;
        width: 100%;
        // border: 1px solid orange;
        // border-radius: 5%;
        color: yellow;
        text-align: center;
        
        position: relative;
        // right: 40%;
        padding-top: 4px;
      }
      &:hover .tooltipText{
        visibility: visible;
      }
    }
  }

}